<template>
  <div class="g-classify">
    <router-link v-for="(v,i) in icons" :to="{name:'classifyDetails',query:{categoryId:v.id,titlename: v.name}}" tag="div" class="m-box">
      <img v-lazy="'https://fx18.oss-cn-hangzhou.aliyuncs.com/'+v.icon" alt="">
      <span>{{v.name}}</span>
    </router-link>
    <nocontent v-show="icons.length==0"/>
  </div>
</template>

<script>
  import nocontent from "@/components/nocontent"
  export default {
    name: "classify",
    data(){
      return{
        icons:[]
      }
    },
    created(){
      this.category();//分类
    },
    methods: {
      category(){
        this.$post(this.$bee+'/api/index/listCategory',{pageSize:1000}).then((data)=>{
          if(data.code==200){
            data.data.categorys.forEach((i)=>{
              this.icons.push(i);
            })
          }else{
            this.$createToast({
              txt: data.message,
              type: 'txt'
            }).show();
          }
        })
      },
    },
    components: {
      nocontent
    }
  }
</script>

<style scoped lang="stylus">
  .g-classify
    display flex
    flex-wrap wrap
    padding-top 20px
    .m-box
      width 25%
      margin-bottom 10px
      img
        width 40px
        height 40px
      span
        font-size 12px
        display block
        margin-top 5px
        color #393939
</style>
